var graceChecker = require("../../graceUI/graceChecker.js");
import LongButton from '../../components/my-components/LongButton.vue';
let requesting = false;
let canLogin = false; //可否登录 
let sessionId = ''
let DataFrom = {}
import {
	wxLogin,
	login
} from './login.js'
export default {
	components: {
		LongButton
	},
	data() {
		return {
			openEye: false,
			userId: '',
			password: '',
			idError: false, //用户信息错误
		}
	},
	computed: {
		disableButton() {
			this.idError = false;
			return !this.userId || !this.password;
		}
	},
	onLoad(option) {
		let that = this;
		DataFrom = this.Cacher.getData(option.from) || {}; //获取页面传参//如果没有from就说明是刚进入小程序
		this.initPage();
		if (!DataFrom.from) {
			wxLogin.call(this).then(res => {
				canLogin = true;
				if (canLogin) {
					uni.reLaunch({
						url: '../../pagesLogin/pages/selectShop?from=login'
					})
				} else {
					this.idError = true; //账号密码不对
				}
			}).catch(res => {}); //微信登录
		} else { //从别处跳转过来的 
			uni.clearStorage(); //清空缓存 
		}
	},
	mounted() {
		this.closePageLoading()
	},
	methods: { 
		clickButton() {
			wxLogin.call(this).then(res => {
				uni.reLaunch({
					url: '../../pagesLogin/pages/selectShop?from=login'
				})
			}).catch(res => { 
				res.message && this.Toast(res.message)
			}); //微信登录
		},
		initPage() {
			this.openEye = false;
			canLogin = false;
			this.userId = 'yilianxinpin';
			this.password = 'Qm8xn4KVBMc0Wd70';
			this.idError = false;
		},
		getUserId(val) {
			this.userId = val.detail;
		},
		getPassWord(val) {
			this.password = val.detail;
		},
		clearInput(key) {
			this[key] = '';
		},
		clickPWIcon() { //切换加密显示与明文显示
			this.openEye = !this.openEye;
		},
		loginWithWx: function() {
			this.closePageLoading();
		},
		loginNow(e) { //点击登录
			if (!requesting) { //函数节流
				requesting = true; //是否正在请求接口
				this.pageLoading();
				login.call(this).then(res => {
					requesting = false;
					uni.reLaunch({
						url: '../../pagesLogin/pages/selectShop?from=login'
					})
				}).catch(res => {
					this.idError = true; //账号密码不对
					requesting = false;
				}); //微信登录; //账号密码登录
			} else {
				setTimeout(() => {
					requesting = false;
					this.Toast('登录时间长，请重试');
				}, 3000)
			}
		},
		reg: function() { //找回密码
			uni.navigateTo({
				url: '../../pagesLogin/pages/index'
			})
		}
	}
}
